@font-face {
    font-family: 'Harry';
    src: url('harry.ttf');
}

@font-face {
    font-family: 'Stark';
    src: url('stark.otf');
}


html,
body {
    padding: 0;
    margin: 0;
    background-color: #000000;
}

#welcome {
    font-family: 'Harry';
    font-size: 46px;
    color: white;
    margin-top: 25px;
}

#center {
    margin: 0 auto;
    text-align: center;
}

#label {
    color: white;
    padding: 0px;
    margin: 0px;
    font-family: 'Stark';
    font-size: 22px;

    text-transform: uppercase;
}

#inputBlock {
    margin-top: 15px;
    margin-bottom: 15px;
}

.inputText {
    border-radius: 8px;
    border-style: solid;
    border-color: black;
    border-width: 2px;
    padding: 5px;
    font-family: 'harry';
    font-size: 30px;
}

.inputText:hover {
    border-style: solid;
    border-width: 2px;
    border-color: teal;
}

.inputText:focus {
    outline: none;
    border-style: solid;
    border-width: 2px;
    border-color: teal;
}

.button {

    padding-top: 5px;
    padding-bottom: 5px;
    padding-left: 15px;
    padding-right: 15px;
    font-family: 'Harry';
    font-size: 36px;
    border-radius: 8px;

    outline: none;
    border-width: 2px;
    border-color: white;
    border-style: solid;
    background: cornsilk;

    margin-left: 5px;
}

.button:focus {
    outline: none;
    border-width: 2px;
    border-color: yellow;
    border-style: solid;
}

#header {
    color: white;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    text-transform: uppercase;
    text-align: center;
    padding:0;
    margin: 0;
    font-size: 10px;
    background-color: #1b1b1b;
}

.title {
    flex-direction: column;
    flex: 1;
}


.contactos {
    color: white;
    font-family: 'harry';
    font-size:  30px;
}

.upMargin {
    margin-top: 5px;
    margin-bottom: 5px;
}

.contacto {
    background:#1b1b1b;
    color: white;
    border-radius: 8px;
    
    margin-bottom: 10px;
    padding: 20px;
}

.nombre {
    padding-bottom  : 10px;
}